
import { _decorator, Component, Node, EventKeyboard, macro, systemEvent, SystemEvent, EventHandler } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('KeyListener')
export class KeyListener {

    private keyCode: {
        right: 37,
        up: 38,
        left: 39,
        down: 40
    }

    eventHandler: EventHandler[] = [];

    registerEvent(type: string, callbackFn: Function) {

    }

    ctor(node: Node) {

        systemEvent.on(SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
        systemEvent.on(SystemEvent.EventType.KEY_UP, this.onKeyUp, this);


        const clickEventHandler = new EventHandler();
        // This node is the node to which your event handler code component belongs
        clickEventHandler.target = node;
        // This is the script class name
        clickEventHandler.component = 'example';
        clickEventHandler.handler = 'callback';
        clickEventHandler.customEventData = 'foobar';

    }

    onKeyDown(event: EventKeyboard) {

        switch (event.keyCode) {

            case this.keyCode.left:
                break;

            case this.keyCode.up:
                break;

            case this.keyCode.right:
                break;

            case this.keyCode.down:
                break;

            default:
                break;
        }

    }

    onKeyUp(event: EventKeyboard) {
        switch (event.keyCode) {

            case this.keyCode.left:
                break;

            case this.keyCode.up:
                break;

            case this.keyCode.right:
                break;

            case this.keyCode.down:
                break;

            default:
                break;
        }
    }

}